@import url(https://fonts.googleapis.com/css2?family=Aldrich&family=Roboto+Mono&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.react-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .react-logo {
    -webkit-animation: react-logo-spin infinite 20s linear;
            animation: react-logo-spin infinite 20s linear;
  }
}

@-webkit-keyframes react-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes react-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


*{
  box-sizing:border-box;
}

* {
  -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */ /* Konqueror HTML */ /* Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome and Opera */
  -webkit-tap-highlight-color: rgba(0,0,0,0);
  -webkit-tap-highlight-color: transparent;
}

* {
  font-family: 'Roboto Mono', monospace;
}

html{
  font-size: 62.5%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

p, span, h1, h2, h3, h4, h5, h6{
  margin:0;
  padding:0;
}

h1, h2, h3, h4{
  font-family: 'Aldrich', sans-serif;
}

h1{
  font-size:4rem;
}
h2{
  font-size:3rem;
}
h3{
  font-size:2.6rem;
}

body{
  margin:0;
  font-family: 'Roboto', cursive;
  font-weight: 500;
  font-size: 1.8rem;
}
